.App {
  text-align: center;
  display: -ms-flexbox;
  display: flex;


}

.NavBar {
  text-align: center;
  font-weight: bold;
  font-family: fantasy;
  font-size: 35px;
  color: purple;

}

.main{
  margin: 35px 35px 0 0;
}

.form-group {
  background-color: purple;
  color: grey;
  font-size: 15px;
  border-radius: 5px;
  padding: 2px;
}

.btn {
  background-color: orange;
  color: grey
}

.border {
border: 10px solid orange;
border-radius: 5px;
}


.btn-primary {
  background-color: white;
  color: grey;
  align: center;
  margin: 5px 0 0 15px;
  font-family: fantasy;

}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



